/* ==========================================================================
   Base
   ========================================================================== */


/* Color
   ========================================================================== */

/* Base color
   -------------------------------------------------------------------------- */
$color-key: #efa336;
$color-accent: #e65100;
$color-background: #fff;
$color-text: #424242;
$color-text--thin: #757575;

/* Opacity
   -------------------------------------------------------------------------- */
$opacity: 0.6;

/* Header
   -------------------------------------------------------------------------- */
$header-background: #fff;
$header-color: #fff;

/* Footer
   -------------------------------------------------------------------------- */
$footer-background: #666;
$footer-color: #fff;

/* Border
   -------------------------------------------------------------------------- */
$form-border-color: #bdbdbd;
$form-border-color--focus: #9e9e9e;

/* Pagination
   -------------------------------------------------------------------------- */
$pagination-background: #eee;
$pagination-background--current: #212121;


/* Size
   ========================================================================== */

/* Layout width
   -------------------------------------------------------------------------- */
$layout-width-canvas: 1440px;
$layout-width-container: 1080px;
$layout-width-smartphone: 768px;

/* Breakpoints (SPbase)
   -------------------------------------------------------------------------- */
$breakpoints-up: (
  "sp": "(max-width: #{$layout-width-smartphone - 1px})",
  "tab": "(min-width: #{$layout-width-smartphone})",
  "pc": "(min-width: #{$layout-width-container})",
);

/* Breakpoints (PCbase)
   -------------------------------------------------------------------------- */
$breakpoints-down: (
  "sp": "(max-width: #{$layout-width-smartphone - 1px})",
  "tab": "(max-width: #{$layout-width-container - 1px})",
  "pc": "(min-width: #{$layout-width-container})",
);

/* Header width
   -------------------------------------------------------------------------- */
$header-height-sp: 60px;
$header-height-pc: 60px;


/* Typography
   ========================================================================== */
  $font-family-hkgw3: "Hiragino Kaku Gothic Pro", "ヒラギノ角ゴ Pro W3", "メイリオ", meiryo, "MS Pゴシック", "MS PGothic", sans-serif;
  $font-family-hkgw6: "HiraKakuPro-W6", "ヒラギノ角ゴ Pro W6", "メイリオ", meiryo, "MS Pゴシック", "MS PGothic", sans-serif;

  $font-family-mincho: "游明朝体", "Yu Mincho", yumincho, "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", "MS P明朝", "MS PMincho", serif;
  $font-family-gothic: "游ゴシック体", "Yu Gothic", yugothic, "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", meiryo, "MS Pゴシック", "MS PGothic", sans-serif;

  $font-family-awesome: "Font Awesome 5 Free", sans-serif;
  $font-family-code: monospace, sans-serif;

  $font-family-base: $font-family-hkgw3;
  $font-size-base: 1.6rem;
  $font-weight-base: 500;
  $line-height-base: 1.6;